
export default {
  name: "CharactersListCard",
  props: {
    id: Number,
    src: String,
    alt: String,
    title: String,
    className: String,
    status: {
      type: String,
      validator(value: string) {
        return ["alive", "dead", "unknown"].includes(value);
      },
    },
  },
  methods: {
    initCharacterPage() {
      this.$store.dispatch("showEpisodeCharacters", false);
    },
  },
};
